<template>
    <div>
        <div style="height:100vh;" class="d-flex justify-content-center align-items-center ">
            <div style="width:100%">
                <div class="row justify-content-center">
                  <div class="col-md-6">
                      <div class="shadow rounded-lg p-3">
                        <div class="sm-form ">
                            <label for="username">username</label>
                            <input type="text" id="username" name="username" class="form-control form-control-sm" placeholder="username" v-model="vdata.username" >
                        </div>                  
                        <div class="sm-form ">
                            <label for="password">password</label>
                            <input type="password" id="password" name="password" class="form-control form-control-sm" placeholder="password" v-model="vdata.password" >
                        </div>      
                        <hr>
                        <button type="button"  class="btn btn-sm btn-style10  " @click="login">Login</button>  
                      </div>
                  </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import autophp from '@/plugins/autophp';
let sdb = new autophp();
let urlnya="https://gis.officialapp.website";
export default{
    data(){
        return{
            vdata:{}
        }
    },
    methods: {
        login(){
             sdb.collection('tbuser',false,urlnya).login2(this.vdata,this.$store,this.$router,'/admin').then(res=>{
                      console.log(res)
                  })
        }
    },
}
</script>